body {
  margin: 0;
  padding: 0;
  color: #333;
  background: #F7F7F7;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 18px;
  font-weight: 200;
}

p{
  line-height: auto;
}

a{
  text-decoration: none;
  color: inherit;
}

ul{
  /* list-style-type: square; */
  /* list-style-position: inside; */
  padding-left: 0;
  list-style: none;
}

ul.list-special li{
  display: inline-block;
  margin-right: 20px;
}

ul.list-special li:before{
  color: #999;  
  font-size: 1em; 
  padding-right: 8px; 
  position: relative;
  top: 0em; 
}

ul.list-special li:first-child:before{
  left: -3px;
  padding-right: 7;
}

ul.list-special li:nth-child(odd):before{
  content: "\25FD"; 
}

ul.list-special li:nth-child(even):before{
  color: #d9d9d9;
  content: "\25CF"; 
  font-size: 0.8em;
  padding-left: 2px;
  top: -1px;
}

h1{
  font-size: 50px;
  text-align: left;
  line-height: 52px;
  font-weight: bold;
  color: #444;
}

h2{
  font-weight: 600;
  font-size: 35px;
}

h3{
  font-weight: 600;
  font-size: 25px;
}

h4{
  font-weight: 500;
  font-size: 22px;
  margin-bottom: 15px;
}

footer{
  color: #999;
  font-size: 15px;
  padding-top: 0 !important;
}

a.link-underline{
  text-decoration: underline;
  font-weight: 600;
  margin-top: 0;
}

