.animate-image {
  position: relative;
  display: inline-block;
  overflow: hidden;
  margin: 0;
  width: 100%;
  color: #fff;
  text-align: left;
  font-size: 16px;
  background: #000;
  border-radius: 5px;
}

.animate-image *,
.animate-image:before,
.animate-image:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.animate-image img {
  max-width: 100%;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  vertical-align: top;
}

.animate-image:before,
.animate-image:after {
  position: absolute;
  top: 20px;
  right: 20px;
  content: '';
  background-color: #fff;
  z-index: 1;
  opacity: 0;
}

.animate-image:before {
  width: 0;
  height: 1px;
}

.animate-image:after {
  height: 0;
  width: 1px;
}

.animate-image figcaption {
  position: absolute;
  left: 0;
  bottom: 0;
  padding: 25px;
}

.animate-image h3 {
  font-weight: 600;
  font-size: 28px;
  margin: 0;
  margin-bottom: 10px;
  opacity: 0;
}

.animate-image h4 {
  font-size: 15px;
  font-weight: normal;
  opacity: 0;
  margin: 0;
  color: #d0d0d0;
}

.animate-image a {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
}

.animate-image:hover img,
.animate-image.hover img {
  zoom: 1;
  filter: alpha(opacity=20);
  -webkit-opacity: 0.2;
  opacity: 0.2;
}

.animate-image:hover:before,
.animate-image.hover:before,
.animate-image:hover:after,
.animate-image.hover:after {
  opacity: 1;
  -webkit-transition-delay: 0.15s;
  -o-transition-delay: 0.15s;
     transition-delay: 0.15s;
}

.animate-image:hover:before,
.animate-image.hover:before {
  width: 40px;
}

.animate-image:hover:after,
.animate-image.hover:after {
  height: 40px;
}

.animate-image:hover h3,
.animate-image.hover h3,
.animate-image:hover h4,
.animate-image.hover h4 {
  opacity: 1;
}

.animate-image:hover h3,
.animate-image.hover h3 {
  -webkit-transition-delay: 0.25s;
  -o-transition-delay: 0.25s;
     transition-delay: 0.25s;
}

.animate-image:hover h4,
.animate-image.hover h4 {
  -webkit-transition-delay: 0.3s;
  -o-transition-delay: 0.3s;
     transition-delay: 0.3s;
}


